<template>
  <div id="canvas_container"></div>
</template>

<script>
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { SVGLoader } from "three/examples/jsm/loaders/SVGLoader";

export default {
  name: "3dviewer",
  data() {
    return {
      width: 500,
      height: 500,
    };
  },
  mounted() {
    const scene = new THREE.Scene();
    const helper = new THREE.GridHelper(160, 10);
    helper.rotation.x = Math.PI / 2;
    scene.add(helper);
    const camera = new THREE.PerspectiveCamera(
      45,
      this.width / this.height,
      1,
      10000
    );
    scene.add(camera);

    const renderer = new THREE.WebGLRenderer({ antialias: true });
    renderer.outputEncoding = THREE.sRGBEncoding;
    renderer.setSize(this.width, this.height);
    document.getElementById("canvas_container").innerHTML = "";

    new ResizeObserver((e) => {
      renderer.setSize(e[0].target.clientWidth, e[0].target.clientHeight);
      this.width = e[0].target.clientWidth;
      this.height = e[0].target.clientHeight;
      camera.aspect = this.width / this.height;
      camera.updateProjectionMatrix();
      camera.updateMatrixWorld();
    }).observe(document.getElementById("canvas_container"));
    renderer.setClearColor(0xbbbbbb, 1);
    const controls = new OrbitControls(camera, renderer.domElement);
    camera.position.set(0, 0, 100);
    camera.lookAt(0, 0, 0);
    controls.update();

    const loader = new GLTFLoader().setPath("models/");
    loader.load("model.gltf", function (gltf) {
      gltf.scene.rotation.x = -Math.PI / 2;
      gltf.scene.rotation.y = Math.PI / 2;
      gltf.scene.position.z += 1.9;
      scene.add(gltf.scene);

      const floader = new SVGLoader();
      floader.load("faces/Front.svg", function (data) {
        const paths = data.paths;
        const group = new THREE.Group();

        for (let i = 0; i < paths.length; i++) {
          const path = paths[i];
          const material = new THREE.MeshPhysicalMaterial({
            color: path.color,
            side: THREE.DoubleSide,
          });

          const shapes = SVGLoader.createShapes(path);

          for (let j = 0; j < shapes.length; j++) {
            const shape = shapes[j];
            const geometry = new THREE.ShapeGeometry(shape);
            const mesh = new THREE.Mesh(geometry, material);
            group.add(mesh);
          }
        }
        group.scale.multiplyScalar(0.000001);
        group.position.z -= 2.8;
        group.position.x -= 29.4 - 3.25;
        group.position.y += 22.2 - 2.4;
        group.rotation.z = -Math.PI / 2;
        scene.add(group);
        floader.load("faces/Back.svg", function (data) {
          const paths = data.paths;
          const group = new THREE.Group();

          for (let i = 0; i < paths.length; i++) {
            const path = paths[i];
            const material = new THREE.MeshPhysicalMaterial({
              color: path.color,
              side: THREE.DoubleSide,
            });

            const shapes = SVGLoader.createShapes(path);

            for (let j = 0; j < shapes.length; j++) {
              const shape = shapes[j];
              const geometry = new THREE.ShapeGeometry(shape);
              const mesh = new THREE.Mesh(geometry, material);
              group.add(mesh);
            }
          }
          group.scale.multiplyScalar(0.000001);
          group.position.z += 1.95;
          group.position.x -= 29.4 - 3.25;
          group.position.y += 22.2 - 2.4;
          group.rotation.z = -Math.PI / 2;
          scene.add(group);
        });
      });
    });

    document
      .getElementById("canvas_container")
      .appendChild(renderer.domElement);

    const lights = [];
    for (let i = 0; i < 6; i++) {
      lights.push(new THREE.DirectionalLight(0xffffff, 1.5));
    }

    lights[0].position.set(10, 0, 0);
    lights[1].position.set(-10, 0, 0);
    lights[2].position.set(0, 10, 0);
    lights[3].position.set(0, -10, 0);
    lights[4].position.set(0, 0, 10);
    lights[5].position.set(0, 0, -10);

    for (let i = 0; i < 6; i++) {
      scene.add(lights[i]);
    }
    renderer.setAnimationLoop(() => {
      controls.update();
      renderer.render(scene, camera);
    });
  },
};
</script>

<style>
#canvas_container {
  height: 100%;
  color: #0b200b;
}
</style>